import React from "react"
import "./contact.scss"
import PersianFlagUp from "../../../persianFlagUp/PersianFlagUp"

const Contact = () => {
  return (
    <div className="contact example">
      <h2 className="example__title ">  </h2>
      <p className="example__description">
                 
      </p>
      <div className="card">
        <img src={"/images/profile1.jpg"} alt="  " />
        <div className="info">
          <span className="name">  </span>
          <span className="tel">
            {PersianFlagUp.formatPhone("09216870717", {
              shouldInsertCountryCode: true,
              toPersian: true,
              beautify: true,
            })}
          </span>
        </div>
        <div className="call-btn">
          <div className="icon-phone"></div>
        </div>
      </div>
      <div className="card">
        <img src={"/images/profile2.jpg"} alt=" " />
        <div className="info">
          <span className="name"> </span>
          <span className="tel">
            {PersianFlagUp.formatPhone("09356782234", {
              shouldInsertCountryCode: true,
              toPersian: true,
              beautify: true,
            })}
          </span>
        </div>
        <div className="call-btn">
          <div className="icon-phone"></div>
        </div>
      </div>
      <div className="card">
        <img src={"/images/profile3.png"} alt=" " />
        <div className="info">
          <span className="name"> </span>
          <span className="tel">
            {PersianFlagUp.formatPhone("09112256897", {
              shouldInsertCountryCode: true,
              toPersian: true,
              beautify: true,
            })}
          </span>
        </div>
        <div className="call-btn">
          <div className="icon-phone"></div>
        </div>
      </div>
    </div>
  )
}

export default Contact
